.TodoTemplate{
    width: 512px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
    border-radius: 4px;
    overflow: hidden;
    font-family: 'Nanum Gothic', sans-serif;
    
    .app-title{
        background-color: #22b8cf;
        color: white;
        height: 4rem;
        font-size: 1.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .content{
        background-color: white;
    }
}

